import { Configuration, OpenAIApi } from "openai";

import log from "@/helpers/log";

let openai;

export default {
  async saveOpenAIKey({ commit }, { key }) {
    try {
      // TODO: validate API Key
      commit("setOpenAIKey", key);
    } catch (error) {
      log.error("error saving OpenAPI API Key: ", error);
      throw error;
    }
  },
  async submitPrompt({ state }, { messages }) {
    const apiKey = state.openAIKey;
    if (!apiKey) {
      throw new Error("No OpenAI API Key set");
    }
    try {
      if (!openai) openai = new OpenAIApi(new Configuration({ apiKey }));
      const completion = await openai.createChatCompletion({
        model: "gpt-3.5-turbo",
        messages,
      });
      return completion.data.choices[0].message;
    } catch (error) {
      console.log("Error with OpenAI API: ", error);
      throw error;
    }
  },
};
