import Vue from "vue";
import Vuex from "vuex";
import VuexPersistence from "vuex-persist";

import { storageAvailable } from "../utils/utils";
import actions from "./actions";
import getters from "./getters";
import mutations from "./mutations";
import defaultState from "./state";

Vue.use(Vuex);

let sessionVuexPersist;
let localStorageVuexPersist;

if (storageAvailable("localStorage")) {
  localStorageVuexPersist = new VuexPersistence({
    key: "chatgpt-enhanced-app",
    storage: window.localStorage,
    reducer: (state) => ({
      openAIKey: state.openAIKey,
      chatList: state.chatList,
    }),
  });
}

if (storageAvailable("sessionStorage")) {
  sessionVuexPersist = new VuexPersistence({
    key: "chatgpt-enhanced-app",
    storage: window.sessionStorage,
    reducer: (state) => ({
      openAIKey: state.openAIKey,
      currentChatId: state.currentChatId,
      chatList: state.chatList,
    }),
  });
}

const plugins = [];
if (sessionVuexPersist) {
  plugins.push(sessionVuexPersist.plugin);
}
if (localStorageVuexPersist) {
  plugins.push(localStorageVuexPersist.plugin);
}

const VuexStore = new Vuex.Store({
  plugins,
  state: defaultState,
  mutations,
  actions,
  getters,
});

export default VuexStore;
