export default {
  setOpenAIKey(state, key) {
    state.openAIKey = key;
  },
  removeOpenAIKey(state) {
    state.openAIKey = "";
  },
  setCurrentChatId(state, chatId) {
    state.currentChatId = chatId;
  },
  addNewChat(state, chat) {
    const old = state.chatList;
    old[chat.id] = chat;
    state.chatList = { ...old };
  },
  addMessageToChat(state, { chatId, message }) {
    const currentChat = state.chatList[chatId];
    if (currentChat) {
      const messages = currentChat.messages ?? [];
      messages.push(message);
      state.chatList[chatId] = { ...currentChat, messages };
    }
  },
  setChatTitle(state, { chatId, title }) {
    const currentChat = state.chatList[chatId];
    if (currentChat) {
      state.chatList[chatId] = { ...currentChat, title };
    }
  },
};
